export default class IndexPage extends React.Component {
  _renderPosts = () => {
    const posts = this.props.data.allPost.edges

    return (
      <Layout>
        <SEO
          title="91.3FM WYEP | WHERE THE MUSIC MATTERS"
          keywords={[
            `radio`,
            `indie`,
            `noncommercial`,
            `independent`,
            `music`,
            `singer songwriter`,
            `festival`,
            `concert`,
          ]}
        />

        <Container>
          <CardList>
            {posts.map(({ node: post }) => (
              <Card key={post.id} {...post} />
            ))}
          </CardList>
        </Container>
      </Layout>
    )
  }

  render() {
    return this._renderPosts()
  }
}
export const query = graphql`
  query {
    allPost {
      edges {
        node {
          title
          image
          link
          path
          columns
          category
          localImage {
            childImageSharp {
              fluid(maxWidth: 500, maxHeight: 350) {
                base64
                tracedSVG
                aspectRatio
                src
                sizes
              }
            }
          }
        }
      }
    }
  }
`