import React from "react"
import styled from "styled-components"
import { Link } from "gatsby"
import Img from "gatsby-image"

const Post = styled.div`
  border: 1px solid ${props => props.theme.colors.secondary};
  border-radius: 2px;
  /* margin: 0 0 1em 0;
  width: 100%; */
  transition: background 0.2s;
  /* @media screen and (min-width: ${props => props.theme.responsive.small}) {
    flex: ${props => (props.featured ? "0 0 100%" : "0 0 49%")};
    margin: 0 0 2vw 0;
  }
  @media screen and (min-width: ${props => props.theme.responsive.medium}) {
    flex: ${props => (props.featured ? "0 0 100%" : "0 0 32%")};
  } */
  &:hover {
    background: ${props => props.theme.colors.tertiary};
  }
  a {
    color: ${props => props.theme.colors.base};
    text-decoration: none;
    background: white;
    .gatsby-image-wrapper {
      height: 0;
      padding-bottom: 60%;
      @media screen and (min-width: ${props => props.theme.responsive.small}) {
        padding-bottom: ${props => (props.featured ? "40%" : "60%")};
      }
    }
  }
`

const Title = styled.h2`
  font-size: 1.5em;
  font-weight: 600;
  margin: 0rem 0.5rem 0.5rem;
`

const Category = styled.h3`
  margin: 0rem;
  color: #bf2e1a;
  font-weight: 600;
  text-transform: uppercase;
  font-weight: 600;
  font-family: "Source Sans Pro", Helvetica, Arial, serif;
  line-height: 20px;
  letter-spacing: 1px;
  font-size: 16px;
  margin: 0rem 0.5rem 0.5rem;
`

const Card = ({ localImage, category, title, link, path, columns }) => {
  if (!columns) {
    columns = "1"
  }
  const postClass =
    "col-md-" + parseInt(columns) * 4 + " col-lg-" + parseInt(columns) * 4
  const aClass =
    "d-flex flex-column align-items-end justify-content-between p-3 block"
  return (
    <div class={postClass}>
      {link && (
        <div class={aClass}>
          <a href={link}>
            {category && (
              <Category dangerouslySetInnerHTML={{ __html: category }} />
            )}
            {localImage && localImage.childImageSharp && (
              <Img
                fluid={localImage.childImageSharp.fluid}
                backgroundColor={"#eeeeee"}
              />
            )}
            <Title dangerouslySetInnerHTML={{ __html: title }} />
          </a>
        </div>
      )}
      {path && !link && (
        <div class={aClass}>
          <a href={path}>
            {category && (
              <Category dangerouslySetInnerHTML={{ __html: category }} />
            )}
            {localImage && localImage.childImageSharp && (
              <Img
                fluid={localImage.childImageSharp.fluid}
                backgroundColor={"#eeeeee"}
              />
            )}
            <Title dangerouslySetInnerHTML={{ __html: title }} />
          </a>
        </div>
      )}
    </div>
  )
}

export default Card
